
import XHeader from 'vux/src/components/x-header/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import  {config} from'../../../../static/js/common.js'
export default {
components: {
XHeader,
XTable,
LoadMore

},
data() {
return {
arr:[],
general_situation:{},
diagnosis:{},
treat_plan:{}
}
},
created(){
this.EmbolectomyAxios();
},
methods:{
EmbolectomyAxios(){
let that=this;

let url= config.domain+ "outreport/treatReport/3?pid=" +config.id;
this.$http
.post(url)
.then((res)=>{
var response =res.data;
that.arr=response.data;
that.general_situation=response.data.general_situation;
that.diagnosis=response.data.diagnosis;
that.treat_plan=response.data.treat_plan;
})
},
}
}



