

import XHeader from 'vux/src/components/x-header/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import  {config} from '../../../static/js/common.js'

let echarts = require('echarts/lib/echarts')

require('echarts/lib/chart/line')

require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')

let times = [];
let systolic =[]; 
let diastolic =[]; 
let dynamicBloodPressure=[];
export default {/*  */
name: 'discharg-report',
components: {
XHeader,
XTable,
LoadMore
},
data() {
return {
arr:[],
diagnosis:[],
treatmentPlan:{},
policy:{},
riskBenefit:{},
strokeScore:{}
}
},
created() {
this.HelloAxios();

},
mounted(){

},
methods: {

HelloAxios(){
let that=this;
let url=config.domain+"outreport/dischargeReport?pid="+config.id;
this.$http
.post(url)
.then((res)=>{
let response =res.data;
that.arr=response.data;
that.diagnosis=response.data.diagnosis;
that.treatmentPlan=response.data.treatmentPlan;
that.policy=response.data.policy;
that.riskBenefit=response.data.riskBenefit;

let dbp =response.data.timedbpsbp.split(";");
let dbps = [];
for (let i in dbp) {
dbps = dbp[i].split(",");
times.push(dbps[0]);
systolic.push(Number(dbps[1]));
diastolic.push(Number(dbps[2]));
dynamicBloodPressure.push([dbps[0],Number(dbps[2])]);
}
this.$nextTick(function(){
this.drawLine();
this.bloodSetOption();
})
})
},
drawLine() {

let dom = this.$refs.mychart;
this.myChart = echarts.init(dom);


this.myChart.setOption({
title: {
text: ''
},
tooltip: {
trigger: 'axis'
},
legend: {
data:['舒张压','收缩压']
},
grid: {
left: '3%',
right: '4%',
bottom: '3%',
containLabel: true
},
toolbox: {
feature: {
saveAsImage: {}
}
},
xAxis: {
type: 'category',
boundaryGap: false,
data:times
},
yAxis: {
type: 'value'
},
series: [
{
name:'舒张压',
type:'line',
stack: '总量',
data:diastolic
},
{
name:'收缩压',
type:'line',
stack: '总量',
data:systolic
}
]
} );

},
bloodSetOption(){
let myChartMonitor = echarts.init(document.getElementById('myChartMonitor'))
let data = dynamicBloodPressure;
let dateList = data.map(function (item) {
return item[0];
});
let valueList = data.map(function (item) {
return item[1];
});
myChartMonitor.setOption({

visualMap: [{
show: false,
type: 'continuous',
seriesIndex: 0,
min: 0,
max: 400
}],


title: [{


}],
tooltip: {
trigger: 'axis'
},
xAxis: [{

}, {
data: dateList,
gridIndex: 1
}],
yAxis: [{
splitLine: {show: true}
}, {
splitLine: {show: false},
gridIndex: 1
}],
grid: [{
}, {
}],
series: [ {
type: 'line',
showSymbol: false,
data: valueList,
xAxisIndex: 1,
yAxisIndex: 1
}]
});
}
},
computed: {

filteredGWTG: function () {
let arr= this.arr.yuhoufengxian.GWTG.riskVal;
if(arr&&arr.length>=2){
arr=arr;
}else{
arr= 0;
}
return arr
},
